import ReactModal from 'react-modal';
import './Modal.css';

function Modal({ isOpen, onRequestClose, children }) {
    return (
        <ReactModal
            ariaHideApp={false}
            isOpen={isOpen}
            onRequestClose={onRequestClose}
            className="Modal"
            overlayClassName="Overlay"
        >
            {children}
        </ReactModal>
    )
}

export default Modal;