function toHuman(value, x = 'x') {
    const groups = [];
    while (value >= 1000) {
        groups.push(value % 1000);
        value = Math.floor(value / 1000);
    };
    groups.push(value);
    groups.reverse();
    return (x || '') + groups.map((group, index) => {
        if (index == 0)
            return group;
        return String(group).padStart(3, '0');
    }).join(',');
};

export default toHuman;