import useAudio from './useAudio';

import accent1 from '../resources/sounds/big-wheel-spin-accent-001.mp3';
import accent2 from '../resources/sounds/big-wheel-spin-accent-002.mp3';
import accent3 from '../resources/sounds/big-wheel-spin-accent-003.mp3';
import accent4 from '../resources/sounds/big-wheel-spin-accent-004.mp3';

import stop1 from '../resources/sounds/big-wheel-spin-stop-001.mp3';
import stop2 from '../resources/sounds/big-wheel-spin-stop-002.mp3';
import stop3 from '../resources/sounds/big-wheel-spin-stop-003.mp3';

import start from '../resources/sounds/big-wheel-spin-start.mp3';

export default function useSounds({ mute }) {
    const globalVolume = 0.45;
    const options = {
        volume: globalVolume,
        mute,
    };
    const stopOptions = {
        ...options,
        volume: globalVolume * 0.75,
    };
    const startOptions = {
        ...options,
        volume: globalVolume * 0.85,
    };
    const [playAccent1] = useAudio(accent1, options);
    const [playAccent2] = useAudio(accent2, options);
    const [playAccent3] = useAudio(accent3, options);
    const [playAccent4] = useAudio(accent4, options);

    const [playStop1] = useAudio(stop1, stopOptions);
    const [playStop2] = useAudio(stop2, stopOptions);
    const [playStop3] = useAudio(stop3, stopOptions);

    const [playStart] = useAudio(start, startOptions);

    return {
        accent: [
            playAccent1,
            playAccent2,
            playAccent3,
            playAccent4,
        ],
        stop: [
            playStop1,
            playStop2,
            playStop3,
        ],
        start: playStart,
    }
}