import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import './BetSlider.css';
import toHuman from '../common/toHuman';

const eventsMove = ['mousemove', 'touchmove'];
const eventsEnd = ['mouseup', 'touchend'];

function BetSlider({ max, value, onChange, enabled }) {
    const [moving, setMoving] = useState(false);
    const ref = useRef();

    const moveListener = useCallback(event => {
        event.stopPropagation();
        if (!ref.current)
            return;
        const rect = ref.current.getBoundingClientRect();
        let x = (event.touches ? event.touches[0].pageX : event.pageX) - rect.left;
        if (x < 0)
            x = 0;
        if (x > rect.width)
            x = rect.width;
        const newValue = Math.round(max * x / rect.width);
        onChange(newValue);
    }, [max, onChange]);

    const endListener = useCallback(event => {
        setMoving(false);
    }, []);

    const handleMouseDown = useCallback((event) => {
        setMoving(true);
        moveListener(event);
    }, [moveListener]);

    useEffect(() => {
        if (!moving)
            return;
        eventsMove.forEach(name => document.body.addEventListener(name, moveListener));
        eventsEnd.forEach(name => document.body.addEventListener(name, endListener));
        return () => {
            eventsMove.forEach(name => document.body.removeEventListener(name, moveListener));
            eventsEnd.forEach(name => document.body.removeEventListener(name, endListener));
        };
    }, [moving, moveListener]);

    const filledStyle = useMemo(() => {
        const width = 100 * value / (max || 1);
        return {
            width: width + '%',
        };
    }, [value, max]);
    const rootStyle = useMemo(() => {
        return {
            visibility: enabled ? 'visible' : 'hidden',
        };
    }, [enabled]);
    return (
        <div ref={ref} className="BetSlider" onMouseDown={handleMouseDown} onTouchStart={handleMouseDown} style={rootStyle}>
            <div className="BetSlider-Value">{toHuman(value)}</div>
            <div className="BetSlider-Filled" style={filledStyle}/>
        </div>
    );
}

export default BetSlider;