import VolumeOffIcon from '@material-ui/icons/VolumeOff';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import PersonIcon from '@material-ui/icons/Person';
import { useCallback } from 'react';
import toHuman from '../common/toHuman';

import './TopTools.css';

function IconButton({ Icon, onClick, className = '', props = {} }) {
    return (
        <div className={"TopTools-IconButton " + className} onClick={onClick}>
            <Icon classes={{ root: "TopTools-Icon" }} {...props} />
        </div>
    );
}

function PlayerName({ value, onClick }) {
    return (
        <div className="TopTools-PlayerName" onClick={onClick}>
            <div className="TopTools-IconButton">
                <PersonIcon classes={{ root: "TopTools-Icon TopTools-PlayerName-Icon"}} stroke="#00000088" />
            </div>
            {String(value)}
        </div>
    );
}

function HighScoresButton({ onClick }) {
    return (
        <div className="TopTools-HighScoresButton" onClick={onClick}>
            ШЕЙХИ
        </div>
    );
}

function PlayerStats({ value }) {
    const { money, place } = value;
    return (
        <div className="TopTools-PlayerStats">
            <div className="TopTools-PlayerStats-Money">{toHuman(money || 0, '$ ')}</div>
            <div className="TopTools-PlayerStats-Place">{`(${place} место)`}</div>
        </div>
    );
}

function TopTools({ noSound, onNoSoundChange, playerName, playerStats, onPlayerNameClick, onHighScoresButtonClick }) {
    const handleNoSoundClick = useCallback(() => {
        onNoSoundChange(!noSound);
    }, [onNoSoundChange, noSound]);
    return (
        <div className="TopTools">
            <div className="TopTools-Left">
                <IconButton props={{ stroke: "#9c7e3fbf" }} Icon={noSound ? VolumeOffIcon : VolumeUpIcon} onClick={handleNoSoundClick}/>
                <HighScoresButton onClick={onHighScoresButtonClick} />
            </div>
            <div className="TopTools-Center">
                <PlayerStats value={playerStats} />
            </div>
            <div className="TopTools-Right">
                <PlayerName value={playerName} onClick={onPlayerNameClick} />
            </div>
        </div>
    )
}

export default TopTools;