const outcomes = [
    1, 5, 1, 3, 1, 20, 1, 3, 1, 5, 1, 3, 1, 10, 1, 3, 5, 1, 5, 1, 3, 1, 10, 1, 3
];
const begins = [
    11, 24, 39, 52.5, 68, 81.7, 97.2, 110.7, 126.4, 140.1, 155.7, 169.2, 185, 198.6, 213.9, 228.2, 242, 257.3, 270.7, 285.8, 299.2, 314.2, 327.3, 342.4, 355.4
];

function degToOutcome(deg) {
    while (deg >= 360)
        deg -= 360;
    while (deg < 0)
        deg += 360;
    for (let i = 0; i < begins.length; ++i) {
        if (deg < begins[i])
            return outcomes[i];
    };
    return outcomes[0];
}

exports = module.exports = degToOutcome;