import clsx from 'clsx';
import oneImage from '../resources/images/1.png';
import threeImage from '../resources/images/3.png';
import fiveImage from '../resources/images/5.png';
import tenImage from '../resources/images/10.png';
import twentyImage from '../resources/images/20.png';
import scrapImage from '../resources/images/scrap.png';
import './Bet.css';
import toHuman from '../common/toHuman';

const images = [
    oneImage,
    threeImage,
    fiveImage,
    tenImage,
    twentyImage,
];

function Bet({ bet, index, onChangeIndex, onClearIndex }) {
    const renderedNumbers = [];
    for (let i = 0; i < images.length; ++i) {
        const image = images[i];
        const count = bet[i];
        const imageClass = clsx({
            'Bet-Item-Image': true,
            'Bet-Item-Image-Selected': i == index,
        });
        const handleClick = onChangeIndex.bind(null, i);
        const handleClearClick = onClearIndex.bind(null, i);
        let countHuman;
        let scrapStyle = {
            visibility: 'hidden',
        };
        if (count > 0) {
            countHuman = toHuman(count);
            delete scrapStyle.visibility;
        };
        renderedNumbers.push(
            <div key={image} className="Bet-Item">
                <div className="Bet-Item-Images-Box">
                    <img src={image} className={imageClass} onClick={handleClick} draggable="false" />
                    <img src={scrapImage} className="Bet-Item-Scrap-Image" draggable="false" onClick={handleClick} style={scrapStyle} />
                </div>
                <div className="Bet-Item-Count" onClick={handleClearClick}>
                    {countHuman || ''}
                </div>
            </div>
        );
    };
    return (
        <div className="Bet">
            {renderedNumbers}
        </div>
    )
}

export default Bet;