import { useEffect, useState, useRef, useCallback } from 'react';

const events = ['mousedown', 'touchstart'];

let globalInteracted = false;
function useInteraction() {
  const [ready, setReady] = useState(globalInteracted);

  useEffect(() => {
    if (ready)
        return;

    const listener = () => {
        setReady(true);
        globalInteracted = true;
    };

    events.forEach((event) => document.addEventListener(event, listener));

    return () => {
      events.forEach((event) => document.removeEventListener(event, listener));   
    };
  }, [ready]);

  return ready;
}

export default function useAudio(url, other) {
    const audioRef = useRef();
    const interacted = useInteraction();
  
    useEffect(() => {
      async function createAudioContext() {
        const { Howl } = await import('howler');
        audioRef.current = new Howl({
            src: url,
            ...other,
        });
      }
  
      if (interacted)
        createAudioContext();
  
      return () => {
        if (audioRef.current)
          audioRef.current.unload();
      };
    }, [url, interacted, other.mute]);
  

    const interactedRef = useRef();
    if (interacted)
        interactedRef.current = interacted;
    const play = useCallback((...args) => {
        const interacted = interactedRef.current;
        const audio = audioRef.current;
        if (!interacted || !audio)
            return;
        audio.play(...args);
    }, []);

    return [play];
  }