import { useCallback, useState, useRef, useEffect, useMemo } from 'react';
import useClickOutside from 'use-click-outside';
import './App.css';

import wheelImage from '../resources/images/wheel.png';
import pointerImage from '../resources/images/pointer.png';

import useSounds from './useSounds';
import degToOutcome from '../common/degToOutcome';
import isValidName from '../common/isValidName';
import Bet from '../Bet/Bet';
import BetSlider from '../BetSlider/BetSlider';
import TopTools from '../TopTools/TopTools';
import Modal from '../Modal/Modal';
import Input from '../Input/Input';
import Button from '../Button/Button';

import useLocalStorage from '@rehooks/local-storage';
import Label from '../Label/Label';

function clamp(value, min, max) {
  return Math.max(min, Math.min(max, value));
}

function randomFromRange(low, high) {
  return low + (high - low) * Math.random();
}

function randomIndex(high) {
  return Math.random() * high | 0;
}

function App() {
  const [degVel, setDegVel] = useState([0, 0]);
  const [bet, setBet] = useState([0, 0, 0, 0, 0]);
  const [money, setMoney] = useState(100000);
  const [place, setPlace] = useState(235);
  const [betIndex, setBetIndex] = useState(null);
  const [noSound, setNoSound] = useLocalStorage('noSound', false);
  const [playerName, setPlayerName] = useLocalStorage('playerName', "Безымянный");
  const [nextPlayerName, setNextPlayerName] = useState('');
  const [openedModal, setOpenedModal] = useState(false);

  const sounds = useSounds({ mute: noSound });

  const requestRef = useRef();
  const prevTimeRef = useRef();
  const soundTimeRef = useRef();

  const animate = (time) => {
    if (prevTimeRef.current !== undefined ) {
      const last = prevTimeRef.current;
      const delta = 1e-3 * (time - last);
      setDegVel(([deg, vel]) => {
        const num = clamp(180 * vel, 0, 180);
        let newVel = vel - delta * clamp(vel / 2, 0.1, 1);
        if (newVel < 0)
          newVel = 0;
        let newDeg = deg + num * delta;
        while (newDeg > 360)
          newDeg -= 360;

        soundTimeRef.current += delta;

        if (soundTimeRef.current > 0.15 / (num * delta)) {
          soundTimeRef.current = 0;
          const soundIndex = randomIndex(sounds.accent.length);
          sounds.accent[soundIndex]();
        }

        if (newVel == 0 && vel != 0) {
          const soundIndex = randomIndex(sounds.stop.length);
          sounds.stop[soundIndex]();
          console.log('OUTCOME', degToOutcome(newDeg));
        }

        return [newDeg, newVel];
      });
    };
    prevTimeRef.current = time;
    requestRef.current = requestAnimationFrame(animate);
  };

  useEffect(() => {
    if (degVel <= 0)
      return;
    soundTimeRef.current = Infinity;
    requestRef.current = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(requestRef.current);
  }, [degVel <= 0]);

  const [deg, vel] = degVel;
  const wheelStyle = {
    transform: 'rotate(' + deg + 'deg)',
  };
  const onStartRotate = useCallback(() => {
    if (vel != 0)
      return;

    sounds.start();

    setDegVel([deg, randomFromRange(7, 10)]);

  }, [deg, vel]);
  
  const handleChangeBetValue = useCallback((newBetValue) => {
    if (betIndex == null || vel != 0)
      return;
    const newBet = bet.slice();
    newBet[betIndex] = newBetValue;
    setBet(newBet);
  }, [bet, betIndex, vel]);

  const handleChangeBetIndex = useCallback((newBetIndex) => {
    if (vel != 0)
      return;
    setBetIndex(newBetIndex);
  }, [vel]);

  const handleClearBetIndex = useCallback((index) => {
    if (vel != 0)
      return;
    const newBet = bet.slice();
    newBet[index] = 0;
    setBet(newBet);    
    setBetIndex(index);
  }, [bet, vel]);

  const handleNoSoundChange = useCallback((newNoSound) => {
    setNoSound(newNoSound);
  }, []);

  const handlePlayerNameClick = useCallback(() => {
    setOpenedModal('rename');
    setNextPlayerName(playerName);
  }, [playerName]);

  const handleHighScoresButtonClick = useCallback(() => {
    setOpenedModal('highScores');
  }, []);

  const handleModalClose = useCallback(() => {
    setOpenedModal(false);
  }, []);

  const handleChangeNextPlayerName = useCallback((newNextPlayerName) => {
    if (!isValidName(newNextPlayerName))
      return;
    console.log(newNextPlayerName);
    setNextPlayerName(newNextPlayerName);
  }, []);

  const handleApplyPlayerName = useCallback(() => { 
    if (!nextPlayerName || !isValidName(nextPlayerName))
      return;
    setPlayerName(nextPlayerName);
    setOpenedModal(false);
  }, [nextPlayerName]);

  const toolsRef = useRef();
  useClickOutside(toolsRef, () => setBetIndex(null));

  useEffect(() => {
    document.body.oncontextmenu = () => false;
  }, []);
  const max = (betIndex == null) ? 0 : (money + bet[betIndex] - bet.reduce((a, v) => a + v, 0));
  const playerStats = useMemo(() => {
    return {
      money,
      place,
    };
  }, [money, place]);
  return (
    <div className="App">
      <TopTools
        noSound={noSound}
        onNoSoundChange={handleNoSoundChange}
        onPlayerNameClick={handlePlayerNameClick}
        playerName={playerName}
        playerStats={playerStats}
        onHighScoresButtonClick={handleHighScoresButtonClick}
      />
      <img src={wheelImage} draggable="false" className="Wheel" style={wheelStyle} onClick={onStartRotate} />
      <img src={pointerImage} className="Pointer" />
      <div className="Tools" ref={toolsRef}>
        <div className="Tools-Item">
          <Bet bet={bet} index={betIndex} onChangeIndex={handleChangeBetIndex} onClearIndex={handleClearBetIndex} />
        </div>
        <div className="Tools-Item">
          <BetSlider max={max} value={bet[betIndex] || 0} onChange={handleChangeBetValue} enabled={betIndex != null} />
        </div>
      </div>
      <Modal
        isOpen={openedModal == 'rename'}
        onRequestClose={handleModalClose}
      >
        <Label caption="Ваше новое имя" />
        <Input value={nextPlayerName} onChange={handleChangeNextPlayerName} autoFocus onPressEnter={handleApplyPlayerName} />
        <Button caption="OK" onClick={handleApplyPlayerName} />
      </Modal>
      <Modal
        isOpen={openedModal == 'highScores'}
        onRequestClose={handleModalClose}
      >
        <Label caption="Шейхи" />
      </Modal>
    </div>
  );
}

export default App;
